import moment from 'moment'
import common from '@/libs/common.mixin'
export default{
	mixins: [common],
	data () {
		return {
			data_uri: 'advices',
			data: []
		}
	},
	methods: {
		formatDate (time) {
			return moment(time).format('YYYY-MM-DD hh:mm')
		}
	}
}



